.container {
  background: #ffffff;
  border: 1px solid #dfe4ea;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 25px;
  height: 100%;
}
.form,
.form_columns,
.form_first_column,
.form_second_column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.label {
  display: block;
  width: 100%;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
}
.input_name,
.input_lastname,
.input_mail,
.input_city {
  height: 30px;
  width: 100%;
  padding: 6px;
  font-size: 14px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
  border: none;
  border-radius: 5px;
}
.input_name,
.input_lastname,
.input_mail {
  background: #f1f1f1;
}
/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .label {
    font-size: 14px;
  }
  .input_name,
  .input_lastname,
  .input_mail,
  .input_city {
    height: 40px;
    padding: 6px;
    font-size: 16px;
  }
}

/*-----DESKTOP-----*/
@media (min-width: 1281px) {
  .container {
    /* max-height: 177px; */
  }
  .label {
    font-size: 14px;
  }
  .form_columns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  .form,
  .form_first_column,
  .form_second_column {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  .input_name,
  .input_lastname,
  .input_mail,
  .input_city {
    height: 40px;
    padding: 6px;
    font-size: 16px;
  }
}
