* {
  box-sizing: border-box;
  margin: 0%;
}
.section {
  width: 100%;
  padding: 18px 20px 100px 20px;
  color: black;
}
.title {
  font-size: 1.5rem;
  line-height: 70px;
  padding-bottom: 5px;
  color: #f0f0f0;
}
.cards {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 20px;
  align-items: center;
  justify-items: center;
}

/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .section {
    padding: 30px 50px 80px 50px;
  }
  .title {
    font-size: 1.7rem;
    line-height: 70px;
    padding-bottom: 5px;
  }
  .cards {
    grid-template-columns: 1fr;
  }
}

/*-----DESKTOP-----*/

@media (min-width: 1281px) {
  .section {
    padding: 35px 150px 100px 150px;
  }
  .container {
    background-repeat: repeat;
    object-fit: cover;
    width: 100%;
  }
  .title {
    font-size: 2.5rem;
    line-height: 70px;
    padding-bottom: 20px;
  }
  .cards {
    grid-template-columns: 1fr 1fr;
  }
}
