* {
  box-sizing: border-box;
  margin: 0%;
}
.container {
  width: 100%;
  height: 100%;
  min-width: 280px;
  /* background-color: rgb(253, 251, 250); */
  background: #f0f0f0;
  /* border: 1px solid #dfe4ea; */
  box-shadow: 0px 5px 5px rgba(83, 83, 83, 0.534);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  /* padding: 15px; */
  grid-auto-columns: auto;
  grid-auto-rows: repeat(2, 1fr);
  grid-auto-flow: column;
  grid-gap: 20px;
  align-items: center;
  justify-items: center;
}
.container:hover {
  transition: 0.3s;
  box-shadow: 0 0 20px 0 rgba(61, 61, 61, 0.3);
}
.image_section {
  position: relative;
  color: white;
}
.image_container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  object-fit: cover;
}
.image {
  /* height: 100%; */
  height: 200px;
  width: 100%;
  object-fit: cover;
  position: relative;
  border-radius: 10px;
}
.icon_heart {
  font-size: 23px;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: pointer;
  z-index: 15;
}
.body_information {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.title {
  font-weight: bold;
  font-size: 1.4rem;
  line-height: 28px;
  color: #383b58;
}
.category {
  font-style: normal;
  font-weight: bold;
  font-size: 1rem;
  line-height: 17px;
  color: #383b58;
  opacity: 0.5;
}
.body_information .details {
  font-weight: 500;
  font-size: 1rem;
  line-height: 20px;
  color: #383b58;
}
.information {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.body_information .description {
  margin-top: 20px;
  color: rgb(0, 0, 0);
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  z-index: 10;
}
.body_information .description.show_all {
  margin-top: 20px;
  height: 100px;
  width: 100%;
  white-space: normal;
  overflow: auto;
}
.btn_show_all {
  font-weight: 600;
  color: rgb(90, 153, 224);
  display: inline-block;
  cursor: pointer;
}
.btn_show_all:hover,
.btn_show_all:active {
  color: rgb(28, 88, 156);
  transition: all 0.3s ease-out;
}
::-webkit-scrollbar {
  width: 5px;
  border-radius: 10px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: rgb(138, 138, 138);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #474747;
}

/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .container {
    width: 100%;
    /* height: 380px; */
    height: 100%;
    /* border: 1px solid #dfe4ea; */
    border-radius: 10px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    justify-items: flex-start;
    grid-template-areas: "a b";
  }
  .image_container {
    width: 100%;
    object-fit: cover;
    grid-area: a;
    align-self: center;
    /* padding-left: 20px; */
  }
  .icon_heart {
    font-size: 28px;
  }
  .body_information {
    grid-area: b;
    align-self: flex-start;
  }
  .title {
    font-size: 1.6rem;
    line-height: 45px;
  }
  .image {
    height: 100%;
    object-fit: cover;
  }
}

/*-----DESKTOP-----*/
@media (min-width: 1281px) {
  .container {
    width: 100%;
    height: 100%;
    min-height: 350px;
    border-radius: 10px;
    background: #f0f0f0;
    /* border: 1px solid #636364; */
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    justify-items: flex-start;
    grid-template-areas: "a b";
  }
  .icon_heart {
    font-size: 28px;
  }
  .image_container {
    width: 100%;
    grid-area: a;
  }
  .image {
    height: 100%;
    object-fit: cover;
  }
  .body_information {
    grid-area: b;
    align-self: flex-start;
  }
  .title {
    font-size: 1.7rem;
    line-height: 35px;
  }
}
