* {
  box-sizing: border-box;
  margin: 0%;
}
.container {
  font-family: "Roboto", sans-serif;
  width: 100%;
  background: #000;
  color: #fff;
  font-weight: bold;
  font-size: 0.75rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 25px;
  text-align: center;
  padding: 100px 20px;
}

.container_search {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.title {
  font-size: 1.5rem;
}
.btn_container {
  width: 100%;
  max-width: 300px;
}
.city_picker,
.day_picker {
  color: white;
  height: 40px;
  width: 300px;
}

/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .container_search {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 15px;
    padding: 20px 30px;
  }
  .container {
    width: 100%;
    color: #fff;
    font-weight: bold;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #000;
    gap: 38px;
  }
  .title {
    padding-top: 50px;
    font-size: 2rem;
  }
  .btn_container {
    width: 160px;
    max-width: 300px;
  }
  .image {
    background: 
    /* linear-gradient(180deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), */ url("../../img/Mercedes-Benz_Maybach_Vision_6_Front_Wine_color.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -120px;
    background-position-x: 50%;

    width: 100%;
    height: 500px;
  }
}

/*-----DESKTOP-----*/
@media (min-width: 1281px) {
  .container_search {
    height: 40px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 20px 60px;
  }
  .city_picker,
  .day_picker {
    height: 40px;
    width: 450px;
  }
  .container {
    background-color: black;
    color: #fff;
    font-weight: bold;
    font-size: 1.05rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 60px;
  }
  .title {
    padding-top: 100px;
    font-size: 2.5rem;
  }
  .btn_container {
    min-width: 200px;
    width: 250px;
  }

  .image {
    background: 
    /* linear-gradient(180deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), */ url("../../img/Mercedes-Benz_Maybach_Vision_6_Front_Wine_color.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -180px;
    background-position-x: 0px;
    width: 100%;
    height: 370px;
  }
}
