* {
  box-sizing: border-box;
  margin: 0%;
}
.container {
  width: 100%;
  height: 100%;
  padding: 150px 15px 0px 15px;
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container_reservation {
  height: auto;
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 50px;
  padding-bottom: 150px;
}
.subtitle {
  color: #ffffff;
  font-size: 1.2rem;
  line-height: 3rem;
  align-self: flex-start;
}
.container_box {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.container_reservationForm,
.container_timePicker,
.container_cardDetailReservation {
  height: auto;
  width: auto;
}

/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .container {
    padding: 150px 12vw 0px 12vw;
  }
  .container_reservation {
    display: flex;
    flex-direction: column;
    gap: 50px;
  }
}

/*-----DESKTOP-----*/
@media (min-width: 1281px) {
  .container_section {
    width: 100%;
    max-width: 1500px;
  }
  .container {
    padding: 150px 6vw 0px 6vw;
  }
  .container_reservation {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-auto-rows: auto;
    gap: 50px;
  }
  .subtitle {
    font-size: 1.6rem;
    line-height: 3.5rem;
  }
}
