@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");

* {
  box-sizing: border-box;
  margin: 0%;
}
.container {
  font-family: "Roboto", sans-serif;
  color: #fff;
  /* height: 90px; */
  height: 130px;
  width: 100vw;
  position: fixed;
  top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(rgb(61, 61, 61), rgba(0, 0, 0, 0));
  /* background: linear-gradient(rgb(180, 180, 180), rgba(0, 0, 0, 0)); */
  padding: 10px 0px 50px 20px;
  z-index: 30;
}
.nav_logo {
  margin-left: 20px;
  text-decoration: none;
  color: #ffff;
  font-size: 2rem;
  align-items: center;
  cursor: pointer;
  flex-grow: 1;
}
.logo {
  height: 40px;
}
.logo_container {
  text-decoration: none;
}
.side_view {
  list-style: none;
}
.container.active_menu {
  background-color: #000;
}
.menu.active .side_view {
  background-color: rgba(0, 0, 0, 0.65);
  height: 100%;
  width: 20%;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 10;
}
.menu.active {
  right: 0px;
  top: 90px;
  display: flex;
}
.nav_items,
.nav_title {
  list-style: none;
  display: none;
}
.menu.active .nav_title {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 80%;
  height: 190px;
  padding: 10px 15px;
  display: flex;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.5rem;
  background: #000000;
  display: flex;
  align-items: flex-end;
  justify-content: right;
}
.menu.active .nav_items {
  position: absolute;
  top: 190px;
  right: 0px;
  width: 80%;
  height: calc(100vh - 190px);
  padding: 0px;
  line-height: 16px;
  background: #f0f0f0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 1;
}
.social_networks {
  position: absolute;
  bottom: 10px;
  right: 15px;
  display: none;
}
.menu.active .social_networks {
  display: flex;
}
.nav_item:first-child {
  margin-right: 15px;
  padding: 28px 0px 28px 0px;
  width: 90%;
  border-bottom: 1.5px solid #000;
}
.nav_item {
  margin-right: 15px;
  padding: 28px 0px 0px 0px;
}
.nav_burger {
  display: block;
  position: absolute;
  right: 0px;
  top: 10px;
  transform: translate(-100%, 60%);
  cursor: pointer;
  color: #ffff;
  font-size: 30px;
}
.nav_burger:active {
  color: #ff7e19;
}
.panelUser {
  color: white;
  display: flex;
  flex-shrink: 0;
  margin: 15px;
}
.imgUser {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: var(--tertiary);
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.imgUserP {
  text-shadow: 1px 1px black;
}
.imgUserPhrase {
  margin-left: 5px;
  text-align: left;
  color: white;
}
.imgUserName {
  color: var(--secondary);
}
.btnLogout {
  position: relative;
  border: none;
  height: 15px;
  width: 15px;
  background-color: transparent;
  color: #000;
  padding: 0;
  margin: 2px;
}

/*-----TABLET-----*/
@media (min-width: 760px) and (max-width: 1280px) {
  .container {
    /* background-color: #000; */
  }
  .logo {
    height: 45px;
    margin: 20px;
  }
  .nav_burger {
    display: none;
  }
  .nav_bar {
    display: flex;
    flex-direction: row;
  }
  .menu {
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
    gap: 15px;
    transition: none;
  }
  .nav_items {
    display: flex;
    justify-content: space-around;
  }
  .nav_item,
  .nav_item:first-child {
    padding: 0px;
    border: none;
  }
  .panelUser {
    margin: 0px;
  }
  .btnLogout {
    position: relative;
    border: none;
    height: 15px;
    width: 15px;
    background-color: transparent;
    color: #ff7e19;
    padding: 0;
    margin: 2px;
    font-weight: bold;
  }
  .imgUserPhrase {
    color: #fff;
  }
}

/*-----DESKTOP-----*/
@media (min-width: 1281px) {
  .container {
    /* background-color: #000; */
  }
  .logo {
    height: 55px;
    margin: 20px;
  }
  .nav_burger {
    display: none;
  }
  .nav_bar {
    display: flex;
    flex-direction: row;
  }
  .nav_title h2 {
    display: none;
  }
  .menu {
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
  }
  .nav_items {
    display: flex;
    justify-content: space-around;
    gap: 15px;
  }
  .nav_item,
  .nav_item:first-child {
    padding: 0px;
    border: none;
    margin-right: 0px;
  }
  .panelUser {
    margin: 0px;
  }
}
